import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Query } from 'react-apollo';

import QuestionRound from './QuestionRound';
import QuestionPreview from './QuestionPreview';
import ErrorAlert from '../Alerts/ErrorAlert';
import DotsLoader from '../Loaders/DotsLoader';
import { Question } from '../../Config/Queries';
import GameOver from '../Game/GameOver';

export default class QuestionsContainer extends Component {
  constructor(props) {
    super(props);

    this.questions = this.props.questions;

    this.state = {
      currentIndex: 0,
      difficulty: null,
      gameScore: 0,
    };
  }

  updateGameScore = (points) => {
    const gameScore = this.state.gameScore + points;
    this.setState({ gameScore });
  }

  advanceQuestion = () => {
    console.log('Advancing question');
    const currentIndex = this.state.currentIndex + 1;
    const difficulty = null;
    console.log(`to ${currentIndex}`);
    this.setState({ currentIndex, difficulty });
  }

  setDifficulty = difficulty => this.setState({ difficulty });

  render() {

    if (this.state.currentIndex > 4) {
      return <GameOver score={this.state.gameScore} />;
    }

    const question = this.questions[this.state.currentIndex];
    return (
      <Query
        query={Question}
        variables={{ id: question.id }}
      >
        {({ loading, error, data }) => {
          if (loading) {
            return <DotsLoader />;
          }

          if (error) {
            return <ErrorAlert message="There was a problem loading this question" />;
          }

          return (
            <React.Fragment>
              { this.state.difficulty
                ? (
                  <QuestionRound
                    questionId={question.id}
                    advanceQuestion={this.advanceQuestion}
                    difficulty={this.state.difficulty}
                    updateGameScore={this.updateGameScore}
                  />
                ) : (
                  <QuestionPreview
                    question={data.question}
                    setDifficulty={this.setDifficulty}
                  />
                )
              }
            </React.Fragment>
          );
        }}
      </Query>
    )
  }
}

QuestionsContainer.propTypes = {
  questions: PropTypes.arrayOf(PropTypes.object).isRequired,
}


