import React, { Component } from "react";
import { Query, Mutation } from "react-apollo";

import history from "../Base/History";
import { GameableCategories, CreateGame } from "../Config/Queries";
import DotsLoader from "../Components/Loaders/DotsLoader";
import ErrorAlert from "../Components/Alerts/ErrorAlert";

export default class Home extends Component {
  render() {
    return (
      <div className="row justify-content-center">
        <div className="col-12 col-md-10 col-lg-8 pt-4">
          <h1 className="my-4 text-center">SortFive</h1>
          <Query query={GameableCategories}>
            {({ loading, error, data }) => {
              if (loading) {
                return <DotsLoader />;
              }

              if (error) {
                return (
                  <ErrorAlert message="There was a problem loading game categories" />
                );
              }

              const gameableCategories = data.gameableCategories;
              console.log({ gameableCategories });

              return (
                <Mutation
                  mutation={CreateGame}
                  update={(cache, result) => {
                    const gameId = result.data.createGame.game.id;
                    history.push(`/game/${gameId}`);
                  }}
                >
                  {(createGame) => (
                    <div className="list-group list-group-flush">
                      {gameableCategories.map((gameCategory) => {
                        const category =
                          gameCategory.questioncategorySet[0].category;

                        return (
                          <button
                            key={category.id}
                            className="list-group-item cursor-pointer"
                            onClick={() => {
                              createGame({
                                variables: { categoryId: category.id },
                              });
                            }}
                          >
                            {category.name}
                          </button>
                        );
                      })}
                    </div>
                  )}
                </Mutation>
              );
            }}
          </Query>
        </div>
      </div>
    );
  }
}



