import React, { Component } from "react";
import PropTypes from "prop-types";

const TIMER_FREQUENCY = 10;
const DEFAULT_DURATION = 5000;
const DEFAULT_HIDE_ON_COMPLETE = false;

export default class Timer extends Component {
  constructor(props) {
    super(props);

    this.onStart = this.props.onStart;
    this.onComplete = this.props.onComplete;
    this.duration = this.props.duration || DEFAULT_DURATION;
    this.showBar = this.props.showBar;
    this.hideOnComplete = this.props.hideOnComplete || DEFAULT_HIDE_ON_COMPLETE;

    this.state = {
      timer: null,
      remainingTime: this.duration,
    };
  }

  componentDidMount() {
    const timer = setInterval(() => this.tick(), TIMER_FREQUENCY);
    this.setState({ timer });

    if (this.onStart) {
      this.props.onStart();
    }
  }

  componentWillUnmount() {
    clearInterval(this.state.timer);
  }

  tick() {
    if (!this.state.timer) {
      return;
    }

    const remainingTime = this.state.remainingTime - TIMER_FREQUENCY;

    if (remainingTime > 0) {
      this.setState({ remainingTime });

      if (this.onTick) {
        this.onTick();
      }
    } else {
      this.setState({ remainingTime: 0 }, () => {
        clearInterval(this.state.timer);

        if (this.onComplete) {
          this.onComplete();
        }
      });
    }
  }

  remaining() {
    return (this.state.remainingTime / this.duration) * 100;
  }

  render() {
    if (this.state.remainingTime === 0) {
      return <span />;
    }

    return this.props.showBar ? (
      <div style={{ display: "flex", width: "100%" }}>
        <div className="timer-container">
          <div className="timer" style={{ width: `${this.remaining()}%` }} />
        </div>
      </div>
    ) : null;
  }
}

Timer.propTypes = {
  duration: PropTypes.number,
  onComplete: PropTypes.func,
  onTick: PropTypes.func,
  showBar: PropTypes.bool,
  hideOnComplete: PropTypes.bool,
};



