import React, { Component } from "react";
import PropTypes from "prop-types";

import AnswerReveal from "./AnswerReveal";

export default class RevealContainer extends Component {
  constructor(props) {
    super(props);
    this.guesses = this.props.guesses;
    this.answers = this.props.answers;
    this.interval = null;
    this.totalScore = this.props.totalScore;

    this.state = {
      revealStarted: false,
      totalScore: null,
      revealedIndex: this.answers.length,
      revealedGuesses: [],
    };
  }

  startReveal = () => {
    this.setState({ revealStarted: true });
    this.interval = setInterval(this.reveal, 1000);
  };

  reveal = () => {
    let { revealedIndex, revealedGuesses } = this.state;
    const guess = this.guesses[revealedIndex];

    if (revealedIndex === 6) {
      window.clearInterval(this.interval);
      this.interval = null;
    }

    if (guess) {
      const answer = this.answers.find((answer) => answer.id === guess.id);

      revealedGuesses.push(
        <AnswerReveal key={guess.id} guess={guess} answer={answer} />
      );
    }

    revealedIndex -= 1;

    if (revealedIndex === 6) {
      window.clearInterval(this.interval);
      this.interval = null;
    }

    this.setState({ revealedGuesses, revealedIndex });
  };

  render() {
    return (
      <div className="reveal-container">
        {!this.state.revealStarted && (
          <div className="times-up">
            <h1>Time's Up!</h1>
            <h3>Let's see how you did</h3>
            <button
              onClick={() => this.startReveal()}
              className="btn btn-secondary"
            >
              See 'em
            </button>
          </div>
        )}

        {this.state.revealStarted && (
          <div>
            {this.state.revealedGuesses.map((guess) => guess)}
            {this.totalScore && (
              <div
                style={{
                  width: "100%",
                  justifyContent: "center",
                  alignItems: "center",
                  display: "flex",
                  flexDirection: "column",
                }}
              >
                <div
                  style={{
                    padding: "2rem",
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "center",
                    alignItems: "center",
                  }}
                >
                  <div style={{ fontSize: "36px" }}>Your Score</div>
                  <div style={{ fontSize: "46px" }}>
                    {this.state.totalScore}
                  </div>
                </div>
                <div style={{ marginTop: "2rem" }}>
                  <a href="/">Back</a>
                </div>
              </div>
            )}
          </div>
        )}
      </div>
    );
  }
}

RevealContainer.propTypes = {
  guesses: PropTypes.arrayOf(PropTypes.object).isRequired,
  answers: PropTypes.arrayOf(PropTypes.object).isRequired,
  totalScore: PropTypes.number,
};



