import React, { Component } from 'react';
import { Query } from 'react-apollo';

import { GameQuestions } from '../Config/Queries';
import ErrorAlert from '../Components/Alerts/ErrorAlert';
import DotsLoader from '../Components/Loaders/DotsLoader';
import QuestionsContainer from '../Components/Questions/QuestionsContainer';

export default class Game extends Component {
  render() {

    const { gameId } = this.props.match.params;
    return (
      <Query
        query={GameQuestions}
        variables={{ gameId: gameId }}
      >
        {({ loading, error, data }) => {
          if (loading) {
            return <DotsLoader />;
          }

          if (error) {
            return <ErrorAlert message="There was a problem loading this category" />;
          }

          const questions = data.gameQuestions.map(gq => gq.question);

          return <QuestionsContainer questions={questions} />;
        }}
      </Query>
    );
  }
}



