import React, { Component } from "react";
import PropTypes from "prop-types";
//import posed from 'react-pose';

import AnswerRow from "./AnswerRow";

export default class AnswerReveal extends Component {
  constructor(props) {
    super(props);

    this.guess = this.props.guess;
    this.answer = this.props.answer;
    this.delay = this.props.delay ? this.props.delay : 500;
    this.index = this.props.index;

    this.state = {
      revealed: false,
    };
  }

  reveal = () => this.setState({ revealed: true });

  componentDidMount() {
    setInterval(this.reveal, this.delay);
  }

  render() {
    return <AnswerRow guess={this.guess} answer={this.answer} />;
  }
}

AnswerReveal.propTypes = {
  guess: PropTypes.object.isRequired,
  answer: PropTypes.object.isRequired,
  delay: PropTypes.number,
  index: PropTypes.number,
};



