import React, { Component } from "react";
import { Query } from "react-apollo";
import PropTypes from "prop-types";
//import _ from 'lodash';

import { QuestionAnswers } from "../../Config/Queries";
import AnswerList from "../Answers/AnswerList";
import Timer from "../Loaders/Timer";
import DotsLoader from "../Loaders/DotsLoader";
import ErrorAlert from "../Alerts/ErrorAlert";
//import gqlClient from '../../Config/GQLClient';

const DIFFICULTY_COUNTS = {
  easy: 5,
  medium: 6,
  hard: 7,
};

const DIFFICULTY_DURATION = {
  easy: 60000,
  medium: 30000,
  hard: 1000,
};

export default class QuestionRound extends Component {
  constructor(props) {
    super(props);

    this.updateGameScore = this.props.updateGameScore;

    this.state = {
      timerStarted: false,
      timerRunning: false,
      timerEnded: false,
      roundScore: null,
    };
  }

  isRunning = () => {
    return (
      this.state.timerStarted &&
      this.state.timerRunning &&
      !this.state.timerEnded
    );
  };

  isPaused = () => {
    return (
      this.state.timerStarted &&
      !this.state.timerRunning &&
      !this.state.timerEnded
    );
  };

  isEnded = () => {
    return (
      this.state.timerStarted &&
      !this.state.timerRunning &&
      this.state.timerEnded
    );
  };

  setRoundScore = (roundScore) => {
    this.setState({ roundScore });
    this.updateGameScore(roundScore);
  };

  onTimerStart = () => {
    this.setState({ timerStarted: true, timerRunning: true });
  };

  onTimerTick = () => {
    console.log("tick");
  };

  onTimerComplete = () => {
    this.setState({ timerRunning: false, timerEnded: true });
  };

  render() {
    const {
      questionId,
      //advanceQuestion,
      difficulty,
    } = this.props;

    let questionContainerClass = "question-container";
    if (this.isRunning()) {
      questionContainerClass += " running";
    }
    if (this.isEnded()) {
      questionContainerClass += " ended";
    }

    return (
      <Query query={QuestionAnswers} variables={{ questionId }}>
        {({ loading, error, data }) => {
          if (loading) {
            return <DotsLoader />;
          }

          if (error) {
            return (
              <ErrorAlert message="There was a problem loading the question" />
            );
          }

          const difficultyCount = DIFFICULTY_COUNTS[difficulty] || 5;
          const questionDuration = DIFFICULTY_DURATION[difficulty] || 1000;
          const answers = data.questionAnswers
            .map((qa) => {
              return {
                id: qa.id,
                text: qa.text,
                helpLong: qa.helpLong,
                helpShort: qa.helpShort,
                order: qa.order,
              };
            })
            .slice(0, difficultyCount);

          const question = data.questionAnswers[0].question;

          return (
            <div
              className="row justify-content-center"
              style={{ height: "100%" }}
            >
              <div
                className="col-12 col-sm-10 col-lg-8"
                style={{ height: "100%" }}
              >
                <div className={questionContainerClass}>
                  <div className="question-header">
                    <h2 className="question">{question.text}</h2>
                    <h3 className="question-context">{question.helpLong}</h3>
                  </div>
                  <div
                    style={{
                      width: "100%",
                      display: "flex",
                      flexWrap: "wrap",
                    }}
                  >
                    <Timer
                      duration={questionDuration}
                      onStart={this.onTimerStart}
                      onTick={this.onTimerTick}
                      onComplete={this.onTimerComplete}
                      showBar={this.isRunning()}
                      hideOnComplete
                    />
                    {this.isRunning() && (
                      <div style={{ width: "100%", textAlign: "center" }}>
                        <button
                          onClick={this.onTimerComplete}
                          style={{
                            background: "#212121",
                            border: "none",
                            borderBottomLeftRadius: "4px",
                            borderBottomRightRadius: "4px",
                            color: "#fff",
                          }}
                        >
                          End Round
                        </button>
                      </div>
                    )}
                  </div>

                  <AnswerList
                    answers={answers}
                    timerRunning={this.state.timerRunning}
                    timerEnded={this.state.timerEnded}
                    setRoundScore={this.setRoundScore}
                  />
                </div>
              </div>
            </div>
          );
        }}
      </Query>
    );
  }
}

QuestionRound.propTypes = {
  questionId: PropTypes.string.isRequired,
  difficulty: PropTypes.string.isRequired,
  advanceQuestion: PropTypes.func,
  updateGameScore: PropTypes.func,
};



