import React, { Component } from "react";
import PropTypes from "prop-types";

export default class AnswerRow extends Component {
  render() {
    const { guess, answer } = this.props;
    const scored = typeof guess.score === "number";

    return (
      <li className={`sortable-row`} data-id={guess.id}>
        {/* {scored && (
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              width: "100%",
            }}
          >
            <div className={`answer-info color${guess.order}`}>
              <div className="index-container">
                <div className={`index `}>{answer.order + 1}</div>
              </div>
              <div className={`answer-container color${guess.order}`}>
                <div className="answer">
                  <div>{answer.text}</div>
                  <div style={{ marginLeft: "0.5rem" }}>
                    ({answer.helpShort})
                  </div>
                </div>
              </div>
              <div
                style={{
                  flex: 0,
                  padding: "0, 0.5rem",
                  justifyContent: "flex-end",
                }}/index-

              >
                <div className="answer">{answer.helpLong}</div>
              </div>
            </div>
            <div className="guess-info">
              <div className={`index-container color${guess.order}`}>
                <div
                  style={{ textTransform: "uppercase", fontSize: "0.75rem" }}
                >
                  Your Guess
                </div>
                <div className={`index inverse`}>{guess.order + 1}</div>
              </div>
              {guess.score > 0 && (
                <div className="score-container">
                  <div className="score bg-danger">
                    {guess.score > 0 && <span>+</span>}
                    {guess.score}
                  </div>
                </div>
              )}
            </div>
          </div>
        )} */}
        <div className="guess-info">
          <div className="index-container">
            <div className={`index color-${guess.order}`}>
              {guess.order + 1}
            </div>
          </div>
          <div className={`answer-container color-${guess.order}`}>
            <div className="answer">
              <strong>{guess.text}</strong>
            </div>
            {!scored && (
              <div className="extra-context">({guess.helpShort})</div>
            )}
          </div>
        </div>
        {scored && (
          <div className="answer-info">
            <div className={`answer-container color-${answer.order}`}>
              <div className="answer">{answer.helpLong}</div>
            </div>
            <div className="index-container">
              <div className={`index`}>
                <div style={{ textSize: "14px" }}>Your Guess:</div>
                <div>{answer.order + 1}</div>
              </div>
            </div>
            <div className="score-container">
              <div
                className={`score ${
                  guess.score === 0 ? "bg-success" : "bg-danger"
                }`}
              >
                {guess.score > 0 && <span>+</span>}
                {guess.score}
              </div>
            </div>
          </div>
        )}
      </li>
    );
  }
}

AnswerRow.propTypes = {
  guess: PropTypes.object.isRequired,
  answer: PropTypes.object.isRequired,
};



