import React, { Component } from "react";
import PropTypes from "prop-types";
import Sortable from "react-sortablejs";
import move from "lodash-move";
import _ from "lodash";

import AnswerRow from "./AnswerRow";
import RevealContainer from "./RevealContainer";

export default class AnswerList extends Component {
  constructor(props) {
    super(props);

    this.answers = this.props.answers;
    this.setRoundScore = this.props.setRoundScore;

    let totalScore;
    let guesses = JSON.parse(JSON.stringify(this.answers));
    _.shuffle(guesses).map((guess, i) => (guess.order = i));

    this.state = { guesses, totalScore };
  }

  componentDidUpdate(prevProps) {
    if (
      this.props.timerEnded &&
      this.props.timerEnded !== prevProps.timerEnded
    ) {
      this.calculateScores();
    }
  }

  updateGuesses = ({ oldIndex, newIndex }) => {
    let guesses = this.state.guesses;
    guesses = move(guesses, oldIndex, newIndex);
    guesses.map((guess, i) => (guess.order = i));

    this.setState({ guesses });
  };

  calculateScores = () => {
    let totalScore = 0;
    const guesses = this.state.guesses.sort((a, b) => a.order - b.order);
    const answers = this.answers;

    _.forEach(guesses, (guess) => {
      const answer = answers.find((answer) => answer.id === guess.id);
      const score = Math.abs(guess.order - answer.order);
      guess.score = score;
      totalScore += score;
    });

    this.setState({ totalScore });
    this.setRoundScore(totalScore);
  };

  render() {
    const { timerRunning, timerEnded } = this.props;
    const disabled = !timerRunning && timerEnded;
    const guesses = this.state.guesses.sort((a, b) => a.order - b.order);

    return (
      <div
        className="list-container"
        style={{ width: "100%", display: "flex", alignItems: "flex-end" }}
      >
        {disabled ? (
          <RevealContainer
            answers={this.answers}
            guesses={guesses}
            score={this.state.totalScore}
          />
        ) : (
          <Sortable
            tag="ul"
            className="sortable-list py-md-4"
            options={{
              onUpdate: this.updateGuesses,
            }}
          >
            {guesses.map((guess) => {
              const answer = this.props.answers.find(
                (answer) => answer.id === guess.id
              );

              return <AnswerRow key={guess.id} guess={guess} answer={answer} />;
            })}
          </Sortable>
        )}
      </div>
    );
  }
}

AnswerList.propTypes = {
  answers: PropTypes.arrayOf(PropTypes.object),
  setRoundScore: PropTypes.func.isRequired,
  timerRunning: PropTypes.bool,
  timerEnded: PropTypes.bool,
};



