import gql from "graphql-tag";


export const Fragments = {
  CategoryInfo: gql`
    fragment CategoryInfo on CategoryType {
      name
      id
      slug
    }
  `,
  QuestionInfo: gql`
    fragment QuestionInfo on QuestionType {
      id
      text
      helpLong
      helpShort
    }
  `,
  AnswerInfo: gql`
    fragment AnswerInfo on AnswerType {
      id
      text
      helpLong
      helpShort
      order
    }
  `,
}


export const CreateGame = gql`
  mutation createGame($categoryId: ID!) {
    createGame(categoryId: $categoryId) {
      result
      game {
        id
      }
    }
  }
`;


export const AllCategories = gql`
  {
    allCategories {
      ...CategoryInfo
    }
  }
  ${Fragments.CategoryInfo}
`;


export const GameableCategories = gql`
  {
    gameableCategories {
      questioncategorySet {
        category {
          ...CategoryInfo
        }
        question {
          ...QuestionInfo
        }
      }
    }
  }
  ${Fragments.CategoryInfo}
  ${Fragments.QuestionInfo}
`;


export const GameQuestions = gql`
  query gameQuestions( $gameId: ID! ) {
    gameQuestions( gameId: $gameId ) {
      question {
        ...QuestionInfo
      }
    }
  }
  ${Fragments.QuestionInfo}
`;

export const CreateCategory = gql`
  mutation createCategory( $name: String!, $slug: String! ) {
    createCategory( name: $name, slug: $slug ) {
      result
      category {
        ...CategoryInfo
      }
    }
  }
  ${Fragments.CategoryInfo}
`;


export const Category = gql`
  query category( $id: ID! ) {
    category( id: $id ) {
      id
      ...CategoryInfo
    }
  }
  ${Fragments.CategoryInfo}
`;


export const CreateQuestionCategory = gql`
  mutation createQuestionCategory( $questionId: ID!, $categoryId: ID!) {
    createQuestionCategory( questionId: $questionId, categoryId: $categoryId ) {
      result
      questionCategory {
        id
        question {
          ...QuestionInfo
        }
        category {
          ...CategoryInfo
        }
      }    
    }
  }
  ${Fragments.QuestionInfo}
  ${Fragments.CategoryInfo}
`;


export const DeleteQuestionCategory = gql`
  mutation deleteQuestionCategory( $id: ID! ) {
    deleteQuestionCategory( id: $id ) {
      result
    }
  }
`;


export const DeleteCategory = gql`
  mutation deleteCategory( $id: ID! ) {
    deleteCategory( id: $id ) {
      result
    }
  }
`;


export const QuestionCategory = gql`
  query questionCategory( $id: ID! ) {
    questionCategory( id: $id ) {
      id
      question {
        ...QuestionInfo
      }
      category {
        ...CategoryInfo
      }
    }
  }
  ${Fragments.QuestionInfo}
  ${Fragments.CategoryInfo}
`;

export const QuestionCategories = gql`
  query questionCategories( $questionId: ID! ) {
    questionCategories( questionId: $questionId ) {
      id
      question {
        ...QuestionInfo
      }
      category {
        ...CategoryInfo
      }
    }
  }
  ${Fragments.QuestionInfo}
  ${Fragments.CategoryInfo}
`;

export const CategoryQuestions = gql`
  query questionCategories( $categoryId: ID! ) {
    questionCategories( categoryId: $categoryId ) {
      id
      question {
        ...QuestionInfo
      }
      category {
        ...CategoryInfo
      }
    }
  }
  ${Fragments.QuestionInfo}
  ${Fragments.CategoryInfo}
`;

export const AllQuestions = gql`
  {
    allQuestions {
      id
      text 
      helpLong
      helpShort
      answerSet {
        ...AnswerInfo
      }
      questioncategorySet {
        category {
          ...CategoryInfo
        }
      }
    }
  }
  ${Fragments.AnswerInfo}
  ${Fragments.CategoryInfo}
`;


export const CreateQuestion = gql`
  mutation createQuestion(
    $text: String!,
    $helpLong: String,
    $helpShort: String, 
  ) {
    createQuestion(
      text: $text,
      helpLong: $helpLong,
      helpShort: $helpShort,
    ) {
      result
      question {
        ...QuestionInfo
      }
    }
  }
  ${Fragments.QuestionInfo}
`;


export const Question = gql`
  query question( $id: ID! ) {
    question( id: $id ) {
      ...QuestionInfo
    }
  }
  ${Fragments.QuestionInfo}
`;


export const QuestionAnswers = gql`
  query questionAnswers( $questionId: ID! ) {
    questionAnswers( questionId: $questionId ) {
      ...AnswerInfo
      question {
        ...QuestionInfo
      }
    }
  }
  ${Fragments.AnswerInfo}
  ${Fragments.QuestionInfo}
`;


export const CreateAnswer = gql`
  mutation createAnswer(
    $questionId: ID!
    $text: String!,
    $helpLong: String,
    $helpShort: String, 
    $order: Int!
  ) {
    createAnswer(
      questionId: $questionId
      text: $text,
      helpLong: $helpLong,
      helpShort: $helpShort,
      order: $order,
    ) {
      result
      answer {
        ...AnswerInfo
      }
    }
  }
  ${Fragments.AnswerInfo}
`;


export const DeleteAnswer = gql`
  mutation deleteAnswer(
    $id: ID!
  ) {
    deleteAnswer(id: $id) {
      result
    }
  }
`;


export const UpdateAnswerOrder = gql`
  mutation updateAnswerOrder(
    $answerId: ID!
    $order: Int!
  ) {
    updateAnswerOrder(
      answerId: $answerId
      order: $order,
    ) {
      result
      answer {
        ...AnswerInfo
      }
    }
  }
  ${Fragments.AnswerInfo}
`;


